.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0);
  border-top: 4px solid #10ed38;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: 100%;
  max-width: 300px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border-radius: 5px;
  padding: 10px 30px;
  font-size: 16px;
}

.breed-select option {
  text-align: center;
}

.breed-select select {
  padding-left: 20px;
  padding-right: 0;
}

.error {
  color: red;
  font-size: 18px;
  font-weight: bold;
  display: none;
}

.cat-image {
  max-width: 500px;
  height: auto;
  padding: 40px 30px;
}

.cat-info-container {
  background-color: #fff;
  margin-left: 20px;
  padding: 40px 30px;
}

